const http = require("http");
const debug = require("debug");
const socketIO = require("socket.io");
const App_1 = require("./App");
const utils_1 = require("../utils/utils");
// import Game from './game/main';
debug('ts-express:server');
let server;
const port = utils_1.normalizePort(3020);
// const game = new Game();
// game.start();
const app = new App_1.default();
app.init().then(() => {
    const express = app.express;
    express.set('port', port);
    server = http.createServer(express);
    server.listen(port);
    try {
        const io = socketIO(server, { serveClient: false });
        io.on('connection', (socket) => {
            utils_1.log(`Connection: ${socket.id}`);
            socket.on('broadcast', (message) => {
                utils_1.log(`Broadcast: ${message}`);
                // game.runScript(message);
                io.emit('broadcast', JSON.stringify(message));
            });
            socket.on('disconnect', () => {
                utils_1.log(`Disconnect: ${socket.id}`);
            });
        });
    }
    catch (error) {
        utils_1.log(`Error: ${error}`);
    }
});
